import { Button } from 'antd-mobile';
import React from 'react';
import { useHistory } from 'react-router-dom';
import {
  AnswerResultList,
  ResultBg,
  ResultButtonBox,
  ResultTitle,
  TrueBox,
  TrueDesc,
  TrueDescItem,
  TruePercent,
  TrueTitle,
} from '../../styles';

const Result: React.FC = () => {
  const history = useHistory();
  return (
    <>
      <ResultBg>
        <ResultTitle>中心考试模拟报告</ResultTitle>
        <TrueBox>
          <TrueTitle>分数</TrueTitle>
          <TruePercent>150</TruePercent>
        </TrueBox>
        <TrueDesc>
          <TrueDescItem>2020年6月数学卷</TrueDescItem>
          <TrueDescItem>用时 08：18</TrueDescItem>
        </TrueDesc>
      </ResultBg>

      <ResultButtonBox>
        <Button
          type="primary"
          inline
          onClick={(): void => history.push('/exam-parsing')}
        >
          查看解析
        </Button>
        <Button type="primary" inline onClick={(): void => history.go(-3)}>
          继续练习
        </Button>
      </ResultButtonBox>
    </>
  );
};

export default Result;
